<script>
	  import LoadingIndicator from './LoadingIndicator.svelte';
	  import Item from './Item.svelte'
	  import dataItems from './items.js'
	
	let isLoading =false

	let data = [];
	let newTodo = '';

	async function addTodo() {
		isLoading=true
		data=[]
		if (!newTodo) return;
		newTodo = newTodo.trim();

		data=dataItems[newTodo.toLowerCase()]


		newTodo = '';
		isLoading=false

	}
</script>

  <section class="section">
    <div class="container">
      <form on:submit|preventDefault={addTodo}>
			<div class="field">
				<div class="control">
					<input class="input is-primary" type="text" aria-label="Enter search item" placeholder="IPhone 11" bind:value={newTodo}>
				</div>
			</div>
		</form>
    </div>
  </section>
<div class="columns is-multiline is-mobile">
		{#if data}
				{#each data as item }
			<Item item={item} ></Item>
		{/each}
		{/if}
	
</div>
	<div class="loading-indicator">
		{#if isLoading}
			<LoadingIndicator />
		{/if}
  </div>

<style>
	main {
		text-align: center;
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
	}

	h1 {
		color: #ff3e00;
		text-transform: uppercase;
		font-size: 4em;
		font-weight: 100;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>